<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process246"></a>

<p><br></p>
<p><span class="rvts17">寻机报仇（</span><span class="rvts50">Seeking Revenge</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:306,Y:964</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">劝阻加雷斯回到复仇女神号或帮助加雷斯杀掉亚历山大</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1368.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在精灵营地第一次遇到亚历山大时，会出现加雷斯为父母报仇的剧情。</span>
<br><span class="rvts21">从剧情考虑，我们会优先选择为他报仇。但是处于长远结局考虑，最好的方法是再次劝阻加雷斯，并让他平静下来回到复仇女神号。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1369.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">如果在死神海岸</span><span class="rvts20"></span><span class="rvts21">埋葬过去</span><span class="rvts20"></span><span class="rvts21">支线中，我们没有顺利劝阻加雷斯回到复仇女神号，那么在这里无论怎样劝阻加雷斯都会以失败告终，并使他最终与亚历山大发生战斗。无论我们站在谁的一边，加雷斯最终都会永远离开我们，不再回到船上。另外，如果在死神海岸选择为加雷斯复仇，杀掉乔纳森，那么最终他也会来无名岛找亚历山大复仇，我们依然是会劝告失败的。</span>
<br><span class="rvts21">相反，如果在死神海岸</span><span class="rvts20"></span><span class="rvts21">埋葬过去</span><span class="rvts20"></span><span class="rvts21">支线中，我们亲眼看到加雷斯埋葬了父母，并成功劝阻他回到船上（帮他找到杀他父母的白魔导师手套证据，或杀掉黑坑洞白魔导师首领再告诉他已经帮他复仇都会使他在该幕无法被劝阻）。那么在这里，我们也有一定几率把他劝回船上去。（经测试，</span><span class="rvts20">5</span><span class="rvts21">劝说</span><span class="rvts20">+15</span><span class="rvts21">力量可以让他回船上。）</span>
<br><span class="rvts21">需要说明的是，加雷斯上不上船的重要性，在于第一他是个战斗技能书商人，第二他会在最后一个章节打最终</span><span class="rvts20">BOSS</span><span class="rvts21">时给予我们祈祷的帮助（如果劝阻失败，他会成为黑暗加雷斯与我们为敌），第三在游戏大结局时，会出现加雷斯的结局场景动画。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



